import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const Header = ({ siteTitle }) => (
  <header>
    <nav className="flex flex-col items-start justify-between bg-black text-white w-full fixed top-0 z-10 md:items-center md:flex-row">
      <div className="text-base m-4 md:m-3 md:text-2xl">
        <h1 className="tracking-widest">
          <Link to="/">{siteTitle}</Link>
        </h1>
      </div>
    </nav>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
